<template>
  <v-card flat color="#fff" class="pb-4" style="height:100vh">
    <div class="row-header no__hidden_class">
      <div class="background-triangle-big">
        <span v-if="String(index) !== '0'">THNG TIN T KHAI</span>
        <span v-else>TO T KHAI THNH CNG</span>
      </div>
      <div class="layout row wrap header_tools row-blue">
        <div class="flex pl-3 text-ellipsis text-bold" style="position: relative;">
        </div>
        <div class="flex text-right" style="margin-left: auto;max-width: 150px;height:37px">
          <v-btn color="primary" class="my-0 mx-0 white--text" v-on:click.native="goBack" style="height:100%">
            <v-icon size="16">reply</v-icon> &nbsp;
            Quay li
          </v-btn>
        </div>
      </div> 
    </div>
    <div class="px-3 mt-3" style="font-size:16px">
      <p class="mb-2 text-bold">Tn t khai: <span class="ml-2 primary--text">{{fileTemplateSelected.templateName}}</span></p>
      <p class="mb-2 text-bold">M t khai: <span class="ml-2 primary--text">E-{{eformDetail.secret}}-{{eformDetail.eFormId }}</span></p>
      <span class="mr-2"><v-icon color="warning">warning</v-icon></span>
      <i>Vui lng ghi li M t khai  tra cu, in li t khai khi cn thit!</i>
      <div class="mt-3">
        <v-btn color="success" class="white--text" 
          @click="previewEform"
          :loading="dialogPDFLoading"
          :disabled="dialogPDFLoading"
        >
          <v-icon>visibility</v-icon> &nbsp;
          Xem trc
        </v-btn>
        <v-btn color="primary" class="ml-3 white--text"
         @click="downloadEform"
          :loading="dialogPDFLoading"
          :disabled="dialogPDFLoading"
        >
          <v-icon>cloud_download</v-icon> &nbsp;
          Ti xung
        </v-btn>
      </div>
      <p class="mt-4">
        -   hon tt th tc,  ngh mang T khai ny cng cc giy t theo quy nh n Cc Lnh s 
        hoc S Ngoi v Thnh ph H Ch Minh hoc Ngoi v a phng hoc 
        C quan i din  xin chng nhn/ hp php ha lnh s.
      </p>
      <p>
        -  T khai ny ch c gi tr hiu lc trong vng 10 ngy lm vic. Qu thi hn trn nu cha np t khai  ngh k khai t khai mi.
      </p>
    </div>
    <!-- <v-flex xs12 class="text-xs-right mt-3">
      <v-btn color="primary" class="ml-3 mr-2 white--text" @click="goBack">
        <v-icon>reply</v-icon> &nbsp;
        Quay li
      </v-btn>
    </v-flex> -->
    <!--  -->
    <v-dialog max-width="1200px" v-model="dialogPDF" transition="fade-transition">
      <v-card>
        <v-toolbar flat dark color="primary">
          <v-toolbar-title>T khai trc tuyn</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-btn icon dark @click.native="dialogPDF = false">
            <v-icon>close</v-icon>
          </v-btn>
        </v-toolbar>
        <div v-if="dialogPDFLoading" style="
            min-height: 700px;
            text-align: center;
            margin: auto;
            padding: 25%;
        ">
          <v-progress-circular
            :size="100"
            :width="1"
            color="primary"
            indeterminate
          ></v-progress-circular>
        </div>
        <iframe v-show="!dialogPDFLoading" id="dialogPDFPreview" src="" type="application/pdf" width="100%" height="100%" style="overflow: auto;min-height: 600px;" frameborder="0">
        </iframe>
      </v-card>
    </v-dialog>
  </v-card>
</template>

<script>

import Vue from 'vue'
import $ from 'jquery'
import toastr from 'toastr'
import axios from 'axios'
import support from '../../store/support.json'
Vue.use(toastr)
export default {
  props: ['index'],
  components: {
  },
  data: () => ({
    serviceInfoList: [],
    eformNoSearch: '',
    formScriptEform: '',
    dialogPDF: false,
    dialogPDFLoading: false
  }),
  computed: {
    serviceinfoSelected () {
      return this.$store.getters.getServiceinfoSelected
    },
    fileTemplateSelected () {
      return this.$store.getters.getFileTemplateSelected
    },
    eformDetail () {
      return this.$store.getters.getEformDetail
    }
  },
  created () {
    var vm = this
    vm.$nextTick(function () {
      let current = vm.$router.history.current
      let currentQuery = current.query
      if (!vm.eformDetail.secret && !vm.eformDetail.eFormNo) {
        vm.goBack()
      }
    })
  },
  updated () {
    var vm = this
    vm.$nextTick(function () {
    })
  },
  watch: {
    '$route': function (newRoute, oldRoute) {
      let vm = this
      let currentParams = newRoute.params
      let currentQuery = newRoute.query
    }
  },
  methods: {
    downloadEform () {
      let vm = this
      vm.dialogPDFLoading = true
      vm.$store.dispatch('downloadEform', vm.eformDetail).then(function (result) {
        vm.dialogPDFLoading = false
      }).catch(function (reject) {
        vm.dialogPDFLoading = false
      })
    },
    previewEform () {
      let vm = this
      vm.dialogPDFLoading = true
      vm.$store.dispatch('previewEform', vm.eformDetail).then(function (result) {
        vm.dialogPDFLoading = false
        vm.dialogPDF = true
        document.getElementById('dialogPDFPreview').src = result
      }).catch(function (reject) {
        vm.dialogPDFLoading = false
      })
    },
    goBack () {
      let vm = this
      vm.$router.push({
        path: '/',
        query: {
          renew: Math.floor(Math.random() * (100 - 1 + 1)) + 1
        }
      })
    }
  }
}
</script>
